		toCategory: function(id, title) {
			var token = uni.getStorageSync('authorization');
			if (token == '' || token == null) {
				uni.showModal({
					cancelText: '取消登录',
					confirmText: '立即登录',
					title: '提示',
					content: '是否确定授权登录？',
					success: function(res) {
						if (res.confirm) {
							uni.navigateTo({
								url: '../myinfo/login'
							})
						} else if (res.cancel) {
							console.log('用户点击取消');
						}
					}
				})
				return
			}
			uni.navigateTo({
				url: '/pages/category/category?categoryId=' + id
			});
		},

