													}
												},
												fail() {
													console.log("打开设置页面失败")
												}
											})
										} else if (res.cancel) {
											// 用户点击取消按钮
											console.log('用户点击取消');
										}
									},
									fail() {
										console.log("确认取消弹出未弹出")
									}
								});
							}
						})


						// 如未授权则重新打开设置页面,进行授权
