				},
				fail() {
					console.log("获取授权信息授权失败")
				}
			})
		},
		// 如果初始进入页面时点击了拒绝授权,如需要该权限,需要手动授权
		// 手动授权的触发方法
		handleLocation() {
			uni.getSetting({
				success(res) {
					if (res.authSetting['scope.userLocation']) {
						// 再次判断所需权限是否已授权,如已授权则直接使用
						if (that.cityname != '请登录') {
							return
						}
						uni.getLocation({
							type: 'wgs84',
							success: function(res) {
								console.log('当前位置的经度：' + res.longitude);
								console.log('当前位置的纬度：' + res.latitude);
								that.location = '' + res.latitude + ',' + res.longitude;
								that.loadBanner();
								that.loadNavList();
								that.loadJuhuasuanlist();
								that.loadCouponList();
								that.location = '' + res.latitude + ',' + res.longitude;
								uni.request({
									url: that.$api_url + '/user/login/loginLocation', //仅为示例，并非真实接口地址。
									method: 'POST',
									header: {
										'Content-Type': 'application/x-www-form-urlencoded',
										'authorization': uni.getStorageSync('authorization') //自定义请求头信息
									},
									data: {
										location: '' + res.latitude + ',' + res.longitude,
										type: '1'
									},
									success: res => {
										console.log(res)
										if (res.data.code == 1000) {
											uni.setStorageSync('Location', res.data.content.city);
											that.cityname = res.data.content.city;
										}
									}
								});
							}
						});
					} else if (!res.authSetting['scope.userLocation']) {
						// 如未授权则重新打开设置页面,进行授权
						uni.showModal({
							//弹出提示框
							title: '是否打开设置页？',
							content: '需要在设置中获取xx信息和xx权限',
							success(res) {
								if (res.confirm) {
									// 用户点击确定按钮
									uni.openSetting({
										// 确认后打开设置页面
										success(res) {
											if (res.authSetting['scope.userLocation']) {
												console.log("手动授权成功")
											} else {
												console.log("手动授权失败")
											}
										},
										fail() {
											console.log("打开设置页面失败")
										}
									})
								} else if (res.cancel) {
									// 用户点击取消按钮
									console.log('用户点击取消');
								}
							},
							fail() {
								console.log("确认取消弹出未弹出")
							}
						});
					}
				},
				fail() {
					console.log("获取已授权信息失败")
				}
			})
		}



	},
	onReady: function() {},
	onShow: function(res) {

		uni.request({
			url: this.$api_url + '/notify/count', //仅为示例，并非真实接口地址。
			method: 'GET',
			header: {
				'authorization': uni.getStorageSync('authorization') //自定义请求头信息
			},
			success: res => {
				console.log(res)
				if (res.data.code == 2002) {
					try {
						uni.clearStorageSync();
					} catch (e) {
						console.log('清除缓存错误：' + e);
					}
				}
			}
		});
		this.getSettingMess();

	},
	onHide: function(e) {

	},
	onLoad: function() {

		that = this;
		const accountInfo = uni.getAccountInfoSync();
		that.appId = accountInfo.miniProgram.appId;
	},
	onReachBottom: function() {
		this.loadCouponList();
	},
	onPullDownRefresh: function() {
		// this.page = 1;
		// this.loadJuhuasuanlist();
		// this.loadCouponList('Refresh');
	}
};
